module.exports = ".rp-container *{-webkit-box-sizing:border-box;box-sizing:border-box}.rp-container fieldset{border:none;margin:0;padding:0}.rp-container{-webkit-box-shadow:0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);bottom:0;box-shadow:0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);cursor:pointer;font-family:Verdana,Geneva,Tahoma,sans-serif;margin:0;padding:0;position:absolute;right:20px;width:280px}.rp-container_minimized .rp-container__wrapper{max-height:0;opacity:1}.rp-container_minimized .rp-container__dismiss{display:none}.rp-container__wrapper{-o-transition:all .2s ease-in-out;-webkit-transition:all .2s ease-in-out;max-height:500px;opacity:1;overflow:hidden;position:relative;transition:all .2s ease-in-out}.rp-container__cover{background-color:#f5f9fb;color:#889189;display:none;height:100%;left:0;padding:20px;position:absolute;text-align:center;top:0;width:100%}.rp-container__header{background-color:#0092eb;color:#fff;padding:15px 20px}.rp-container__dismiss{color:#fff;cursor:pointer;font-family:Arial,sans-serif;font-size:14px;font-weight:lighter;position:absolute;right:8px;text-decoration:none;top:8px}.rp-container__dismiss:after{content:\"✖\"}.rp-container__cover .rp-container__field{padding:20px 0}.rp-container__field{border:none;border-bottom:1px solid transparent;font-family:Verdana,Geneva,Tahoma,sans-serif;margin:0;padding:10px 0 0;width:100%}.rp-container__button:focus,.rp-container__field:focus{outline:0}.rp-container__button{background-color:#0092eb;border:none;color:#fff;cursor:pointer;letter-spacing:1px;padding:12px 35px 10px;text-transform:uppercase;width:100%}.rp-container__message{background-color:#f5f9fb}.rp-container__message .rp-container__field{background-color:transparent;min-height:80px;resize:none}.rp-container label{color:#889189;display:block;font-family:Verdana,Geneva,Tahoma,sans-serif;font-size:12px;padding:20px}.rp-container__actions{padding:20px}"